.describe {
  position: relative;
	padding: 235px 0 235px 0;
	min-height: 817px;
  box-shadow: inset 0 -14px 0 0 #000;
  z-index: 1;

	@media (max-width: 1024px) {
		padding: 60px 0 350px 0;
		min-height: 100%;
	}

  @media (max-width: 639px) {
    padding: 0px 0 350px 0;
  }

	.page-section__bg-img {
		@media (max-width: 1024px) {
			height: 365px;
			top: auto;
			bottom: 0;
		}
	}

  &__bg {
    position: relative;
    position: absolute;
    width: 50%;
    height: 100%;
    bottom: 0;
    left: 0;
    margin-right: 50%;
    z-index: 1;

    &::before {
      position: absolute;
      content: '';
      bottom: 0;
      left: 0;
      width: 100%;
      height: 14px;
      background-color: #000;
    }

    @media (max-width: 1024px) {
      width: 100%;
      height: 378px;
      margin-right: 0;
    }

    img {
      position: absolute;
      width: auto;
      height: 100%;
      bottom: 0;
      right: 0;
      z-index: -1;

      @media (max-width: 1024px) {
        right: 50%;
        width: auto;
        height: 100%;
        transform: translate(50%, 0);
      }
    }
  }

	&__container {
		justify-content: flex-end;
	}

	&__text {
		padding: 0;
	}

	&__title {
		margin-bottom: 50px;
	}
}
