@font-face {
  font-family: 'Museo Sans';
  
  src: url('../fonts/MuseoSans-100.woff') format('woff'),
       url('../fonts/MuseoSans-100.otf')  format('opentype'),
       url('../fonts/MuseoSans-100.ttf')  format('truetype');
  font-weight: 100;
  font-style: normal;
}

